import "core-js/modules/es.array.push.js";
import router from '@/router';
import { getBookShopData } from "../assets/api/home";
import { reactive, toRefs } from '@vue/reactivity';
export default {
  setup() {
    let data = reactive({
      arry: null,
      forth: null,
      sixth: null,
      morestudy: null,
      moreforth: null,
      moresixth: null
    });

    //要循环的
    let getShopFun = () => {
      getBookShopData().then(res => {
        data.arry = res.data.results.list;
        //所有的
        // console.log(data.arry);
        data.arry.forEach(item => {
          item["show"] = false;
        });
        //四级
        data.forth = data.arry.filter(item => {
          return item.title.includes("四级");
        });
        //六级
        data.sixth = data.arry.filter(item => {
          return item.title.includes("六级");
        });
        //考研
        data.morestudy = data.arry.filter(item => {
          return item.title.includes("考研");
        });
        //专四
        data.moreforth = data.arry.filter(item => {
          return item.title.includes("专四");
        });
        //专四
        data.moresixth = data.arry.filter(item => {
          return item.title.includes("专八");
        });
      });
    };
    getShopFun();

    //学习返回
    let study = () => {
      router.push({
        path: "/study"
      });
    };
    //视频
    let video = item => {
      console.log(item);
      router.push({
        path: "/video"
      });
      window.localStorage.setItem("datas", JSON.stringify(item));
    };
    return {
      getShopFun,
      video,
      study,
      ...toRefs(data)
    };
  }
};